







































	export default {
		data() {
			return {
				SessionKey: '',
				OpenId: '',
				nickName: null,
				avatarUrl: null,
				isCanUse: uni.getStorageSync('info') || true ,//默认为true
			    getphone:"1",
				writePhone:false,
				phoneNumber:"",  //手机号
			};
		},
		methods: {
			//第一授权获取用户信息===》按钮触发
			wxGetUserInfo(detail) {


				let _this = this;
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
					  // console.log("phon",infoRes)
						_this.nickName = infoRes.userInfo.nickName; //昵称
						_this.avatarUrl = infoRes.userInfo.avatarUrl; //头像
							
						try {
							//uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
							_this.updateUserInfo();
							_this.getphone=2;
						} catch (e) {}
					},
					fail(res) {}
				});
			},

   
             getPhoneNumber (e) {
            	  var _this=this;
            	  uni.getStorage({
            	  	  key:"sessionkey",
            		  success:function(res){
            			  
            		  }
            	  })
            		   
               if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {    
                  //  console.log('用户拒绝提供手机号');  
                } else {  
            		_this.writePhone=true;
                    
                }
               
            },
            
			//登录
			login() {
				let _this = this;
				uni.showLoading({
					title: '登录中...'
				});

				// 1.wx获取登录用户code
				uni.login({
					// provider: 'weixin',
					success: function(loginRes) {
						console.log(loginRes)
						let code = loginRes.code;

						if (!_this.isCanUse) {
							//非第一次授权获取用户信息
							uni.getUserInfo({
								provider: 'weixin',
								success: function(infoRes) {
									// console.log("1",infoRes)
									//获取用户信息后向调用信息更新方法
									let nickName = infoRes.userInfo.nickName; //昵称
									let avatarUrl = infoRes.userInfo.avatarUrl; //头像
									
									//_this.updateUserInfo(); //调用更新信息方法
								}
							});
						}

					//	2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
						uni.request({
							url: "http://shanpei.wsstreet.net/getOpenid",
							data: {
								code: code,
							},
							method: 'POST',

							success: (res) => {
								console.log("res",res)
								_this.OpenId=res.data.data.openid;
								_this.SessionKey=res.data.data.session_key;
								uni.setStorage({
									key:"sessionkey",
									data:_this.SessionKey
								})
								//openId、或SessionKdy存储//隐藏loading
								uni.hideLoading();
							}
						});
					},
				});
			},
			//向后台更新信息
			updateUserInfo() {
			    let _this = this;
			    uni.request({
			        url:"http://shanpei.wsstreet.net/reg" ,//服务器端地址
			        data: {
			            openid:_this.OpenId,
						headimg:_this.avatarUrl,
						nickname:_this.nickName
			        },
			        method: 'POST',
			       
			        success: (res) => {
						//console.log( "22",res)
			            if (res.data.code==1) {
							var info=JSON.stringify(res.data.data)
							uni.setStorage({
								key:"info",
								data:info,
								success:(res)=>{
									console.log("用户信息保存成功")
								}
							})

			            }
			        }
			       
			    });
			},
			cancel(){
				this.writePhone=false;
				
			},
			//确认跳转home
			confirm(){
				    console.log(1)
                
				var result = this.phoneNumber.replace(/(^\s+)|(\s+$)/g,"");
				if(result.length<1){
					console.log(2)
					uni.showToast({
						title:"请输入手机号",
						duration:1000
					})
				}else if(!(/^1[3456789]\d{9}$/.test(this.phoneNumber))){
					console.log(3)
					uni.showToast({
						title:"请输入正确的手机号",
						duration:1000
					})
				}else{
					console.log(4)
					uni.request({
						url:this.config.url+"reg",
						method:"POST",
						data:{
							openid:this.OpenId,
							headimg:this.avatarUrl,
							nickname:this.nickName,
							phone:this.phoneNumber
						},
						success:(res)=>{
							console.log("reg",res)
							if(res.data.code==1){
								var userinfo=JSON.stringify(res.data.data)
								uni.setStorage({
									key:"info",
									data:userinfo,
									success:()=>{
										uni.reLaunch({
											url:"/pages/tabBar/home"
										})
									}
								})
							}
						}
					})
				}
			}
		},
		onLoad() { //默认加载
		   
		     uni.getStorage({
		     	key:"info",
				success:(res)=>{
					uni.reLaunch({
						url:"/pages/tabBar/home"
					})
				
				},
				fail:()=>{
					
					this.login();
				}
		     })
			
		}
	}
